/* Fonts  */
@font-face {
  font-family: "ArialSpec";
  src: url("/css/Fonts/ArialSpec.ttf");
}

@font-face {
  font-family: "Front-Page-Neue";
  src: url("/css/Fonts/FrontPageNeue.otf");
}

@font-face {
  font-family: "NexaBold";
  src: url("/css/Fonts/NexaBold.otf");
}
@font-face {
  font-family: "Nilland";
  src: url("/css/Fonts/Nilland.ttf");
}
@font-face {
  font-family: "Roboto-BoldCondensed";
  src: url("/css/Fonts/Roboto-BoldCondensed.ttf");
}

html{
  font-size: 16px;
}

p {
  color: #002745;
}

.justify{
  text-align:justify
}

#landing{
  display: flex;
  justify-content: center;
  align-items: center;

  background-image: url('/images/background-image.jpg');
  background-position: center;
  background-size: cover;

  min-height: 100vh;
  width: auto;
}

#landing > h1 {
  color: #fff;
  font-size: 4rem;
  font-family: 'Front-Page-Neue';
  font-weight:normal;
  font-style:normal;
  margin-top: 50px;
}

#landing > h1 > span {
  color: #00FFFF;
}

#decoration-1{
  position: absolute;
  height: 30px;
  width: 100%;
  background-color: #00FFFF;

  top: 0;
  z-index: 1;
}

#decoration-2{
  position: absolute;
  height: 90px;
  width: 90px;
  background-color: #002745;
  top: 0;
  left: 15%;
  z-index: 2;
}

/* Content */

.txt-1 {
  font-family: 'NexaBold';
}

.txt-2 {
  font-family: 'ArialSpec';
}


.section-content p, .section-content h2 {
  margin-top: 60px;
  color: #002745;
}

/* SEO */
#seo-section{
  position: relative;
  padding-top: 50px;
  background-image: url('/images/gusano1.png');
}

#seo-section h2{
  color: #002745;
  font-size: 8rem;
  font-family: 'Front-Page-Neue';

}

.sub-text{
  font-size: 2rem;
  font-family: 'Nilland';
}

.img-seo{
  position: absolute !important;
  bottom: 0;
}

.service-square {
  margin: 10px 0px;
}

.service-square h3 {
  font-family: 'Front-Page-Neue';
  margin-top: 20px;
  margin-bottom: 20px;
  color: #002745;
}

.service-square  {
  padding-top: 70px;
}
.service-square p {
  text-align: justify !important;
  color: #002745;
  padding: 0px 15px;
  font-family: 'ArialSpec';
  line-height: 1.3;
}

.service-square img {
  max-width: 300px;
  height: auto;
}

#seo-dots {
  z-index: 2;
  transform: translateY(33%);
}

/* footer */
#footer {
  width: 100%;
  height: 100%;
}

@media (max-width:767px) {
  #landing > h1 {
    margin-top: 35vh;
    font-size: 2rem;
  }

  #seo-section h2 {
    font-size: 3rem;
  }

  .service-square img{
    max-height: 150px;
  }

  .dots{
    max-height: 100px;
  }
}
